<script>
    if(window['ctmCid'] !== 0){
        window['ctmCid'] = 0;
    }
    export default {
        props: ['className'],
        methods: {
            //TODO modal toast loading 
            mountMyself (id = 'module') {
                //nextTickios8loadingloaded
                //
                this.$nextTick(() => {
                    this.$blocker = document.createElement('div');
                    window['ctmCid']++;
                    this.$blocker.id = id + '-ctm-' + window['ctmCid'];
                    document.body.appendChild(this.$blocker);
                    this.$mount('#'+this.$blocker.id);
                });
            },
             mountMyself2 (id = 'module') {
                //ps. nextTickmodalroot
                //loading
                //this.$nextTick(() => {
                    this.$blocker = document.createElement('div');
                    window['ctmCid']++;
                    this.$blocker.id = id + '-ctm-' + window['ctmCid'];
                    document.body.appendChild(this.$blocker);
                    this.$mount('#'+this.$blocker.id);
                //});
            },
            destroyMyself () {
                this.$destroy();
            }
        },
        beforeDestroy () {
            if(this.$el && this.$el.parentNode)
                this.$el.parentNode.removeChild(this.$el);
        }
    }
</script>


